<template>
  <div class="container my-4">
    <h1>
      <b><u>CRUD - My Adoption Center</u></b>
    </h1>

    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <!-- Formulario para editar entradas -->
    <form @submit.prevent="editarMascota(mascotaEditar)" v-if="editar">
      <br />
      <h3>Editar información de mascota</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
        v-model="mascotaEditar.nombre"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Tipo"
        v-model="mascotaEditar.tipo"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Género"
        v-model="mascotaEditar.genero"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Tamaño"
        v-model="mascotaEditar.tamano"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Edad (meses)"
        v-model="mascotaEditar.edad"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
        v-model="mascotaEditar.descripcion"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Ruta de imagen"
        v-model="mascotaEditar.ruta_img"
      />
      <b-button class="btn-success m-2" type="submit">Editar</b-button>
      <b-button class="btn-danger" type="submit" @click="editar = false"
        >Cancelar</b-button
      >
    </form>

    <!-- Formulario para generar entradas -->
    <form @submit.prevent="agregarMascota()" v-if="!editar">
      <br />
      <h3>Agregar una nueva mascota</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
        v-model="mascota.nombre"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Tipo"
        v-model="mascota.tipo"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Género"
        v-model="mascota.genero"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Tamaño"
        v-model="mascota.tamano"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Edad (meses)"
        v-model="mascota.edad"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
        v-model="mascota.descripcion"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Ruta de imagen"
        v-model="mascota.ruta_img"
      />
      <b-button class="btn-success my-2" type="submit">Agregar</b-button>
    </form>

    <!-- Tabla de resultados -->
    <br /><br />
    <h3>Registro de las mascotas</h3>
    <table class="table">
      <thead>
        <tr>
          <!--th scope="col">#</th-->
          <th scope="col">Nombre</th>
          <th scope="col">Tipo</th>
          <th scope="col">Género</th>
          <th scope="col">Tamaño</th>
          <th scope="col">Edad</th>
          <th scope="col">Descripción</th>
          <th scope="col">Ruta imagen</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in mascotas" :key="index">
          <!--th scope="row">{{ item._id }}</th-->
          <td>{{ item.nombre }}</td>
          <td>{{ item.tipo }}</td>
          <td>{{ item.genero }}</td>
          <td>{{ item.tamano }}</td>
          <td>{{ item.edad }} meses</td>
          <td>{{ item.descripcion }}</td>
          <td>{{ item.ruta_img }}</td>
          <td>
            <b-button class="btn-warning m-2" @click="activarEdicion(item._id)"
              >Editar</b-button
            >
            <b-button class="btn-danger" @click="eliminarMascota(item._id)"
              >Eliminar</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      mascotas: [],
      mascota: {
        nombre: "",
        tipo: "",
        genero: "",
        tamano: "",
        edad: "",
        descripcion: "",
        ruta_img: ""
      },
      editar: false,
      mascotaEditar: {},
    };
  },

  created() {
    this.listarMascotas();
  },

  methods: {
    listarMascotas() {
      this.axios
        .get("/mascota")
        .then((res) => {
          this.mascotas = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    agregarMascota() {
      this.axios
        .post("/mascota", this.mascota)
        .then((res) => {
          this.mascotas.push(res.data);
          this.mascota.nombre = "";
          this.mascota.tipo = "";
          this.mascota.genero = "";
          this.mascota.tamano = "";
          this.mascota.edad = "";
          this.mascota.descripcion = "";
          this.mascota.ruta_img = "";
          this.mensaje.color = "success";
          this.mensaje.texto = "Mascota agregada";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    eliminarMascota(id) {
      this.axios
        .delete(`mascota/${id}`)
        .then((res) => {
          const index = this.mascotas.findIndex(
            (item) => item._id === res.data.id
          );
          this.mascotas.splice(index, 1);
          this.mensaje.color = "success";
          this.mensaje.texto = "Mascota eliminada";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    activarEdicion(id) {
      this.editar = true;
      this.axios
        .get(`mascota/${id}`)
        .then((res) => {
          this.mascotaEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    editarMascota(item) {
      this.axios
        .put(`mascota/${item._id}`, item)
        .then((res) => {
          const index = this.mascotas.findIndex((n) => n._id === res.data._id);
          this.mascotas[index].nombre = res.data.nombre;
          this.mascotas[index].tipo = res.data.tipo;
          this.mascotas[index].genero = res.data.genero;
          this.mascotas[index].tamano = res.data.tamano;
          this.mascotas[index].edad = res.data.edad;
          this.mascotas[index].descripcion = res.data.descripcion;
          this.mascotas[index].ruta_img = res.data.ruta_img;
          this.mensaje.color = "success";
          this.mensaje.texto = "Mascota editada";
          this.showAlert();
          this.editar = false;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },

    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
};
</script>